<template>
  <div id="depart_box">
    <div class="flex p20">
      <div
        v-if="deptNameArr && deptNameArr.length > 0"
        style="display:flex;align-items:center;"
      >
        <!-- @click="routerGo(i)" -->
        <div
          v-for="(item,i) in deptNameArr"
          :key="i"
          style="display:flex;align-items:center;"
        >{{ item.deptName }}
          <van-icon name="arrow" />
        </div>
      </div>
    </div>
    <div class="depart_content">
      <!-- 用户 -->
      <div
        v-if="departOtherAll.length > 0"
        class="depar_center"
      >
        <div
          v-for="(item,i) in departOtherAll"
          :key="i"
          class="flex depart_list space"
        >
          <div
            class="flex"
            style="align-items:end;"
          >
            <img
              :id="'Img' + item.id"
              style="border-radius:50%;width:25px;height:25px;margin-right:6px;"
            />
            <canvas
              :id="'headImage' + item.id"
              style="display: none"
            ></canvas>
            <div>
              <div>{{ item.realName || item.nickName }}</div>
            </div>
          </div>
          <div class="flex">
            <img
              class="pr12 h16"
              @click="more('staff',item)"
              src="@/assets/department/operation.png"
            >
            <van-icon
              class="font12"
              name="arrow"
            />
          </div>
        </div>
      </div>
      <!-- 子部门 -->
      <div
        v-if="departOrderAll.length > 0"
        class="depar_center"
      >
        <div
          v-for="(item,i) in departOrderAll"
          :key="i"
          @click="goNext(item)"
          class="flex depart_list space"
        >
          <div
            class="flex"
            style="align-items:end;"
          >
            <img
              class="pr12 pt2"
              src="@/assets/department/depart.png"
            >
            <div>
              <div>{{ item.deptName }}</div>
              <div class="font12">{{ item.staffNum }}人</div>
            </div>
          </div>
          <div class="flex">
            <img
              class="pr12 h16"
              @click.stop="more('depart',item)"
              src="@/assets/department/operation.png"
            >
            <van-icon
              class="font12"
              name="arrow"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="flex depart_bottom">
      <div
        class="w50"
        @click="addDepart"
      >
        <van-icon
          class="pr12"
          color="#FF7D41"
          name="plus"
        />新建部门
      </div>
      <div
        class="w50"
        @click="addPerson('depart','/add-departStaff')"
      >
        <van-icon
          class="pr12"
          color="#FF7D41"
          name="plus"
        />新建人员
      </div>
    </div>
    <!-- 更多操作 -->
    <van-popup
      v-model="show_operation"
      :close-on-click-overlay='false'
      position="bottom"
      class='operation_box'
    >
      <div class="operation_content">
        <div
          v-if="type == 'depart'"
          @click="editDepart('edit')"
        >编辑部门</div>
        <div
          v-if="type == 'depart'"
          @click="addChildDepart"
        >添加子部门</div>
        <div
          v-if="type == 'depart'"
          @click="addStaff"
        >添加人员</div>
        <div
          v-if="type == 'staff'"
          @click="editDepart('change')"
        >变更部门</div>
        <div
          style="color:red;"
          @click="remove"
        >{{ type == 'depart'?'删除部门':'删除员工' }}</div>
      </div>
      <div
        class="cancel"
        @click="show_operation = false"
      >取消</div>
    </van-popup>
    <!-- 更多操作-新增、编辑部门or变更部门 -->
    <van-dialog
      v-model="show_editDepart"
      :before-close="confirmDepart"
      :title="title"
      show-cancel-button
    >
      <div v-if="edit_or_change == 'edit'">
        <div class="flex edit_top">
          <div class="w30">部门名称</div>
          <input
            v-model="top_input"
            type="text"
            class="w60 input"
            placeholder="请输入部门名称"
          >
        </div>
        <div class="flex edit_top">
          <div class="w30">上级部门</div>
          <input
            v-model="bottom_input"
            readonly
            class="w60 input"
          >
        </div>
      </div>
      <!-- 变更部门 -->
      <div v-else>
        <div class="flex edit_top">
          <div class="w30">员工姓名</div>
          <input
            v-model="top_input"
            readonly
            class="w60 input"
          >
        </div>
        <div class="flex edit_top">
          <div class="w30">所属部门</div>
          <select
            class="w60"
            style="border:none;"
            v-model="bottom_input"
            @change="chooseDepartment"
          >
            <option
              v-for="(item,index) in departmentList"
              :key="index"
              :value="item.deptName"
            >{{ item.deptName }}</option>
          </select>
        </div>
      </div>
    </van-dialog>
  </div>
</template>
<script>
import { GetSystemDeptByuser, GetSystemDeptParent, GetSystemDeptAllByuser, PostSystemDeptAdd, PostSystemDeptEdit, PostSystemDeptRemove, PostSystemDeptUserM, PostSystemDeptUserRemove } from '@/api/api_depart';
import { Toast } from 'vant';
import { draw } from '@/utils/avatar.js';
export default {
  data() {
    return {
      show_operation: false,
      show_editDepart: false,
      departData: null,
      type: null,
      title: null,
      top_input: null,
      bottom_input: null,
      deptForm: {
        parentId: null,
        id: null,
        deptName: null
      },
      staffForm: {
        userId: null,
        deptId: null
      },
      edit_or_change: 'edit',
      departmentList: [],
      departOrderAll: [],
      deptNameArr: [],
      departOtherAll: []
    };
  },
  created() {
    this.deptNameArr = this.$store.state.deptNameArr;
    if (!this.$route.query.parentId) {
      this.deptNameArr = [];
    } else {
      let deptNameIndex = null;
      for (let i = 0; i < this.deptNameArr.length; i++) {
        if (this.$route.query.parentId == this.deptNameArr[i].id) {
          deptNameIndex = i;
          break;
        }
      }
      if (deptNameIndex) {
        let newDeptNameArr = this.deptNameArr.filter((item, index) => index < deptNameIndex);
        this.deptNameArr = newDeptNameArr;
      }
    }
    this.getCurrentInfo();
  },
  methods: {
    getCurrentInfo() {
      if (this.$route.query.parentId) {
        GetSystemDeptParent({ parentId: this.$route.query.parentId }).done((res) => {
          this.departData = res.data; // 部门
          this.departOtherAll = res.other; // 用户
          this.departOrderAll = res.order; // 子部门
          this.$nextTick(function () {
            this.departOtherAll.forEach((item) => {
              if (!item.avatar) {
                draw(40, 40, 20, 'headImage' + item.id, 'Img' + item.id, item.realName || item.nickName, 1);
              }
            });
          });
          this.$store.commit('setHeader', this.departData.deptName);
          let filterDeptArr = this.deptNameArr.filter((item) => item.id == this.departData.id);
          if (filterDeptArr.length == 0) {
            this.deptNameArr.push(this.departData);
            this.$store.commit('SET_DEPT_MENU', this.deptNameArr);
          }
          this.getdepartAll();
        });
      } else {
        GetSystemDeptByuser().done((res) => {
          this.departData = res.data; // 部门
          this.departOtherAll = res.other; // 用户
          this.departOrderAll = res.order; // 子部门
          this.$nextTick(function () {
            this.departOtherAll.forEach((item) => {
              if (!item.avatar) {
                draw(40, 40, 20, 'headImage' + item.id, 'Img' + item.id, item.realName || item.nickName, 1);
              }
            });
          });
          this.deptNameArr = [];
          this.deptNameArr.push(this.departData);
          this.$store.commit('SET_DEPT_MENU', this.deptNameArr);
          this.$store.commit('setHeader', this.departData.deptName);
          this.getdepartAll();
        });
      }
    },
    getdepartAll() {
      GetSystemDeptAllByuser().done((res) => {
        this.departmentList = res.data;
      });
    },
    addDepart() {
      this.top_input = null;
      this.title = '新建部门';
      this.bottom_input = this.departmentList.find((item) => item.id == this.departData.id).deptName;
      this.deptForm.parentId = this.departData.id;
      this.deptForm.id = null;
      this.show_editDepart = true;
    },
    addPerson(type, url) {
      this.$router.push({
        path: url,
        query: {
          deptId: type == 'depart' ? this.departData.id : this.choosed_item.id,
          deptName: type == 'depart' ? this.departData.deptName : this.choosed_item.deptName
        }
      });
    },
    more(type, item) {
      this.bottom_input = null;
      this.top_input = null;
      this.deptForm.parentId = null;
      this.deptForm.id = null;
      this.deptForm.deptName = null;
      this.type = type;
      this.choosed_item = item;
      this.show_operation = true;
    },
    editDepart(edit_or_change) {
      this.edit_or_change = edit_or_change;
      this.title = edit_or_change == 'edit' ? '编辑部门' : '变更部门';
      if (edit_or_change == 'edit') {
        this.top_input = this.choosed_item.deptName;
        this.bottom_input = this.departmentList.find((item) => item.id == this.departData.id).deptName;
        this.deptForm.id = this.choosed_item.id;
        this.deptForm.parentId = this.departData.id;
      } else {
        this.staffForm.userId = this.choosed_item.id;
        this.top_input = this.choosed_item.realName;
        this.staffForm.deptId = this.departData.id;
        this.bottom_input = this.departmentList.find((item) => item.id == this.departData.id).deptName;
      }
      this.show_operation = false;
      this.show_editDepart = true;
    },
    addChildDepart() {
      this.title = '添加子部门';
      this.bottom_input = this.choosed_item.deptName;
      this.deptForm.parentId = this.choosed_item.id;
      this.deptForm.id = null;
      this.show_operation = false;
      this.show_editDepart = true;
    },
    addStaff() {
      this.show_operation = false;
      this.addPerson('staff', '/add-departStaff');
    },
    remove() {
      if (this.type == 'depart') {
        PostSystemDeptRemove({ deptId: this.choosed_item.id }).done((res) => {
          Toast(res.msg);
          this.show_operation = false;
          this.getCurrentInfo();
        });
      } else {
        PostSystemDeptUserRemove({ deptId: this.choosed_item.id }).done((res) => {
          Toast(res.msg);
          this.show_operation = false;
          this.getCurrentInfo();
        });
      }
    },
    chooseDepartment() {
      this.id = this.departmentList.find((item) => item.deptName == this.bottom_input).id;
      this.staffForm.deptId = this.id;
      this.staffForm.userId = this.choosed_item.id;
    },
    goNext(item) {
      this.$router.push({
        path: '/staffDepart-management',
        query: {
          parentId: item.id
        }
      });
    },
    routerGo(index) {
      index = index + 1;
      let goIndex = this.deptNameArr.length - index;
      this.$router.go(goIndex);
    },
    confirmDepart(action, done) {
      if (action == 'confirm') {
        if (this.edit_or_change == 'edit') {
          this.deptForm.deptName = this.top_input;
          if (!this.deptForm.id) {
            if (!this.top_input) {
              Toast('请填写部门名称');
              done(false);
              return;
            }
            PostSystemDeptAdd(this.deptForm).done((res) => {
              done(false);
              this.show_editDepart = false;
              this.getCurrentInfo();
            });
          } else {
            PostSystemDeptEdit(this.deptForm).done((res) => {
              done(false);
              this.show_editDepart = false;
              this.getCurrentInfo();
            });
          }
        } else {
          PostSystemDeptUserM(this.staffForm).done((res) => {
            Toast(res.msg);
            done(false);
            this.show_editDepart = false;
            this.getCurrentInfo();
          });
        }
      } else if (action == 'cancel') {
        done(false);
        this.show_editDepart = false;
      }
    }
  }
};
</script>
<style scoped>
#depart_box {
    color: #000;
    font-size: 0.8rem;
}
.flex {
    display: flex;
    align-items: center;
}
.depart_content {
    position: fixed;
    top: 110px;
    bottom: 50px;
    left: 0;
    right: 0;
    overflow: auto;
}
.depart_list {
    border-bottom: 1px solid #efefe4;
    padding: 10px 20px;
    text-align: left;
    line-height: 24px;
    height: 40px;
}
.depart_bottom {
    /* border: 1px solid red; */
    justify-content: space-around;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    line-height: 40px;
    background: #fff;
}
.w50 {
    border: 1px solid #efefe4;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.depar_center {
    background: #fff;
}
.operation_box {
    width: 100%;
    line-height: 45px;
}
.operation_content {
    border-bottom: 1px solid #efefe4;
}
.cancel {
    border-top: 6px solid #f1eff2;
}
.editDepar_box {
    width: 80%;
}
.edit_top {
    padding: 0 16px;
    color: #737373;
    font-size: 0.7rem;
    text-align: left;
    margin: 20px 0;
}
.space {
    justify-content: space-between;
}
.font12 {
    font-size: 0.7rem;
    color: #b2b2b2;
}
.input {
    color: #000;
}
.w30 {
    width: 25%;
}
.w60 {
    width: 72%;
    height: 30px;
    line-height: 32px;
    background: #f0eff4;
    text-indent: 1rem;
}
.p20 {
    padding: 20px;
    color: #393939;
}
.h16 {
    height: 16px;
}
.pr12 {
    padding-right: 12px;
}
.pt2 {
    padding-top: 2px;
}
/deep/ .van-dialog__confirm {
    color: #ff7d41;
}
</style>



